@import "valid";
@import "grid";
@import "animation";

.card_title{
    margin: 70px 0;

    color: $black;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.03em;
}

.block__card{
    display: grid;
    &_product{
        display: grid;
        grid-template-columns: 150px 50px 245px 155px 600px;
        grid-template-rows: 200px;
        margin: 30px 0;

        &_img{
            grid-column: 1/2;
            background-color: $gray__product-back;
            width: 150px;
            height: 200px;
            text-align: center;
            padding: 5px 0;
            
            img{
                width: 65px;
                height: 190px;
            }
        }
        &_info{
            // background-color: $blue;
            grid-column: 3/4;

            margin-top: 60px;
            &_name{
                color: $black;
                font-family: Montserrat;
                font-style: normal;
                font-weight: bold;
                font-size: 16px;
                line-height: 20px;
                letter-spacing: 0.03em;
            }
            &_article{
                color: $gray__text;
                font-family: Inter;
                font-style: normal;
                font-weight: 300;
                font-size: 14px;
                line-height: 17px;
            }

            &_size{
                margin-top: 30px;

                color: $black;
                font-family: Inter;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 17px;
            }
        }
        &_price{
            // background-color: $blue;
            grid-column: 5/6;
            display: flex;
            justify-content: space-between;
            align-content: center;
            margin-top: 60px;

            .price-for-one{
                user-select: none;
            }

            p{
                color: $gray__text;
                font-family: Inter;
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 24px;
            }
            .counter{
                display: flex;
                justify-content: space-between;
                align-items:center;

                width: 90px;
                height: 30px;
                background-color: $gray__product-back;

                .count{
                    user-select: none;

                    color: $black !important;
                    font-size: 18px;
                    line-height: 22px;
                }
                .minus{
                    margin-left: 10px;
                    margin-bottom: 10px;
                }
                .plus{
                    margin-right: 10px;
                }
            }
            .end_price_product{
                user-select: none;
                color: $black;
            }
            .delete_product{
                margin-top: 5px;

                font-size: 12px;
                line-height: 15px;
            }
        }
    }
    &_order{
        margin-top: 70px;
        margin-bottom: 150px;

        display: grid;
        grid-template-columns: 430px 167px 105px 148px 350px;

        &_info{
            display: flex;
            align-items: center;

            color: $black;
            font-family: Inter;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 19px;
            text-decoration-line: underline;

            .phone{
                display: none;
            }
        }
        &_end-price{
            grid-column: 3/4;
            
            display: flex;
            align-items: center;

            color: $black;
            font-family: Inter;
            font-style: normal;
            font-weight: 500;
            font-size: 24px;
            line-height: 29px;

            .total-text{
                display: none;

                text-transform: uppercase;
                font-family: Montserrat;
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 24px;
                // color: $gray__text;
            }
        }
        &_append{
            grid-column: 5/6;
            cursor: pointer;

            display: flex;
            justify-content: center;
            align-items:center;
            background-color: $blue;
            color: white;
            width: 350px;
            height: 60px;

            font-family: Inter;
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 22px;
           
        }
    }
}

.block__append-order{
    @extend %display_none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    // height: 900px;
    background-color: white;
    &_title{
        margin-top: 50px;
        margin-bottom: 70px;
        display: flex;
        justify-content: space-between;
    }
    &_form{
        margin-bottom: 50px;
    }
}

@media (min-width:320px) and (max-width: 1224px){
    .block__card_product{
        grid-template-columns: auto auto auto auto auto;
    }
    .block__card_order{
        grid-template-columns: auto auto auto auto auto;
    }
}

@media (max-width: 768px){
    .card_title{
        margin: 30px 0;
    }
    .block__card_product{
        margin: 30px auto;
        // display: grid;
        // justify-content: center;
        min-width: 280px;
        max-width:500px;

        grid-template-columns: auto;
        grid-template-rows: auto;
        grid-template-areas: 
            "info"
            "img"
            "price";
        &_img{
            grid-area: img;
            grid-column: auto;

            margin: 15px 0;
            width: 100%;
            height: 300px;
            // padding: 0;
            img{
                width: auto;
                height: 280px;
            }
        }
        &_info{
            grid-area: info;
            grid-column: auto;
            
            margin: 0;
            
            &_size{
                display: none;
            }
        }
        &_price{
            grid-area: price;
            grid-column: auto;

            margin: 0;

            display: flex;
            flex-wrap: wrap;

            .multiply{
                display: none;
            }

            .price-for-one{
                width: 100%;
                margin-bottom: 40px;
            }
            .delete_product{
                display: none;
            }

        }
    }
    .block__card_order{
        grid-template-columns: auto;
        grid-template-areas: 
            "end-price"
            "append"
            "info";

        margin: 70px 0;
        &_info{

            grid-area: info;
            grid-column: auto;

            display: flex;
            justify-content: center;
            margin-top: 20px;

            .desktop{
                display: none;
            }
            .phone{
                display: block;
            }
        }

        &_end-price{
            grid-area: end-price;
            grid-column: auto;

            display: flex;
            justify-content: space-between;
            margin-bottom: 30px;
            .total-text{
                display: block;
            }
        }

        &_append{
            grid-area: append;
            grid-column: auto;

            width: 100%;
            
        }
    }
}

@media (min-width:769px) and (max-width: 891px){
    .block__card_product{
        grid-template-columns: 120px auto 150px auto 350px;
        &_img{
            // grid-area: img;
            // grid-column: auto;

            // margin: 15px 0;
            width: 100%;
            // height: 300px;
            // padding: 0;
        }
    }
    .block__card_order{
        grid-template-columns: 250px auto 105px auto 250px;
        &_append{
            width: 250px;
        }
    }
}

@media (min-width:892px) and (max-width: 1024px){
    .block__card_product{
        grid-template-columns: 150px auto 200px auto 400px;
        &_img{
            // grid-area: img;
            // grid-column: auto;

            // margin: 15px 0;
            width: 100%;
            // height: 300px;
            // padding: 0;
            
        }
    }
    .block__card_order{
        grid-template-columns: 280px auto 120px auto 300px;
        &_append{
            width: 300px;
        }
    }
}


@media (min-width:1025px) and (max-width:1224px) {
    .block__card_product{
        grid-template-columns: 150px auto 220px auto 500px;
    }
    .block__card_order{
        grid-template-columns: 430px auto 105px auto 350px;
    }
}
