.container__catalog{
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto;
    grid-gap: 100px 75px;
    justify-content: center;
}

.container__product_preview{
    display: grid;
    grid-template-columns: 682px 350px;
    grid-template-rows: 777px;
    grid-column-gap:168px;
    
    margin-top: 70px !important;
 }
 

.container__product_info{
    display: grid;
    grid-template-columns: 125px 527px 352px;
    grid-template-rows: 366px;
    grid-column-gap:99px;
    
    margin-top: 150px !important;
    margin-bottom: 200px !important;

    // -webkit-background-clip: text;
    // -webkit-text-fill-color: transparent;
}

.container__card{
    display: block;
    
}

.container{
    max-width: 1200px;
    margin: auto;
}

.container__append-to-card{
    display: grid;
    grid-template-columns: 600px;
    justify-content: center;
}

.container__size{
    display: grid;
    grid-template-columns: 1200px;
    justify-content: center;
}

.container__append-order{
    display: grid;
    grid-template-columns: 600px;
    justify-content: center;
}

@media (max-width: 480px){
    .container{
        margin: 0 20px;
    }
    .container__catalog{
        grid-template-columns: auto auto;
        grid-template-rows: auto;
        grid-gap: 35px 10px;
    }
}

@media (min-width:321px) and (max-width:375px) {
    .container__catalog{
        grid-gap: 35px 15px;
    }
}

@media (min-width:376px) and (max-width:480px) {
    .container__catalog{
        grid-gap: 45px 25px;
    }
}

@media (min-width:481px) and (max-width:768px) {
    .container{
        margin: 0 40px;
    }
}

@media (min-width:481px) and (max-width:640px) {
    .container{
        grid-template-columns: auto auto;
    }
    .container__catalog{
        grid-column-gap: 30px;
    }
}


@media (min-width:641px) and (max-width:768px){
    .container__catalog{
        grid-column-gap: 30px;
    }  
}

@media (min-width:769px) and (max-width:1024px){
    .container{
        margin: 0 50px;
    }
    .container__catalog{
        grid-template-columns: auto auto;
        grid-column-gap: 50px;
    }
}

@media (min-width:1025px) and (max-width:1224px){
    .container{
        margin: 0 70px;
    }
    .container__catalog{
        grid-column-gap: 40px;
    }
}



